Canvas {
    border: 1px solid black;
    display: block;
    margin: auto;
    margin-bottom: 15px;
}

html,
body {
    height: 100%;
    background: #121212;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: #fdfdfd;
    text-align: center;
    padding: 5px;
}

h1 {
    width: 100%;
    background: #000000;
    padding: 10px;
}

.italics {
    font-style: italic;
}

.TopSplit,
.BottomSplit {
    padding: 15px;
    margin: 15px;
    border-radius: 3px;
    background: rgba(255, 255, 255, 0.05);
    color: #fdfdfd;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.CharacterCreator {
    position: absolute;
    width: 100%;
    height: 101%;
    text-align: center;
    background: #121212;
}

.downloadButton {
    margin: 15px;
}

.card-title {
    font-weight: bold;
    text-shadow: 2px 2px 4px #000000;
}

.part-selector-button {
    padding: 5px;
    margin: 5px;
}